@import url(https://fonts.googleapis.com/css2?family=Berkshire+Swash&family=Noticia+Text&family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


html, body{
    width: 100%;
    height: 100%;
}
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;;
    text-decoration: none;
    outline: none;
}
:root{
    --light-bg-color: #ffe8d6;
    --bg-color: #ddbea9;
    --txt-color: #5e503f;
}
.page-div{
    display: flex;
    justify-content: center;
    padding-bottom: 20px;
    background-color: #ddbea9;
    background-color: var(--bg-color);
    height:100%;
}
.page{
    margin-top: 20px;
    font-family: 'Roboto', sans-serif;
    width: 95%;
    max-width: 50rem;
    text-align: justify;
    padding-bottom: 20px;
    padding: 20px;
    border-radius: 10px;
    color : #5e503f;
    color : var(--txt-color);
    background-color: #ffe8d6;
    background-color: var(--light-bg-color);
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}
h4, h3{
    margin: 10px 0;
    font-weight: 400;
    font-family: 'Noticia Text', serif;
}
h2{
    margin-top: 20px;
}
p{
    margin: 10px 0;
}
.duration{
    font-style: italic;
    font-size: 0.9rem;
    margin-top: 0;
}
h3{
    margin-top: 20px;
    margin-bottom: 5px;
}

.lang-selector label{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    color: var(--txt-color) !important;
}
.lang-selector label select{
    border: 1px solid var(--txt-color);
    color: var(--txt-color);
    border-radius: 5px;
    padding: 2px;
    margin-left: 2px;
    background-color: var(--bg-color);
}
.lang-selector label option{
    padding: 5px;
    color: var(--bg-color);
    background-color: white;
}

.header, .header *{
    --bg-color: #a47148;
    --txt-color: #f3d5b5;
    
}
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px;
    background-color: var(--bg-color);
}
.header nav{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.header .name{
    color: var(--txt-color);
    font-size: 2rem;
    font-weight: 600;    
    font-family: 'Berkshire Swash', cursive;
}
.header ul{
    display: flex;
    justify-content: space-between;
    width: 14rem;
    list-style: none;
    margin-left: 5px;
    margin-top : 10px;
}
.header ul li{
    position: relative;
}
.header ul li *{
    color: var(--txt-color);
}
.header ul li:after{
    content: "";
    width: 0;
    height: 1.5px;
    background-color: var(--txt-color);
    position: absolute;
    top: 105%;
    left : 0;
    transition-duration: 0.5s;
}
.header ul li:hover::after{
    width: 100%;
}
.controls{
    display: flex;
    align-items: center;
    justify-content: space-around;
}

@media screen and (max-width: 467px) {
    .header{
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
    }
    .header ul{
        margin : 10px 0;
    }
}
